.icon {
  width: 30px;
  margin-right: 11px;
}

h1 {
  margin: 0;
}

.userPhoto {
  width: 250px;
  height: 250px;
  border-radius: 20px;
  grid-area: avatar;
}

.profileInfo {
  display: grid;
  grid-template-areas: "avatar contacts";
  grid-template-columns: 300px;
}
@media (min-width: 768px) {
  .contacts {
    margin-top: 45px;
  }
}

@media (max-width: 767px) {
  .app-wrapper {
    grid-template-columns: 100px 1fr;
    max-width: none;
  }
  .profileInfo {
    display: block;
  }
  .contacts {
    margin-bottom: 25px;
    margin-top: 25px;
  }
}

.contacts {
  grid-area: contacts;
  font-size: 25px;
}

.fullName {
  margin-bottom: 10px;
}

.editInfo {
  margin: 10px;
}
